@import './common';
@import '_reset.scss';

.offer_text {
  font-size: 12px;
  line-height: 35px;
  text-transform: uppercase;
  font-weight: 600;
  position: relative;
  letter-spacing: -0.5px;
  margin-left: 10px;
  height: 35px;
  top: 0;
  color: #fff;
  margin: 0 auto;
  text-align: center;
  background: #800000;
}

.offer_text > span {
  padding-left: 12px;
  padding-right: 12px;
}


// ? nav section

.top_menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  height: 50px;
}

.top_menu > li > a {
  display: block;
  text-transform: uppercase;
  color: #000;
  font-size: 14px;
  line-height: 1.3em;
  text-decoration: none;
}

.top_menu > li {
  margin: 0 15px;
}
.offer_section.bottom .product_block:before,
.offer_section.bottom .product_block:after {
  display: none;
}

.offer_section.bottom .product_block {
  margin: 25px 0 0;
  padding: 0;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  overflow: hidden;
}

.offer_section.bottom .product_block > img {
  margin-left: -50px;
}

.offer_section.bottom .product_block .woman {
  margin: 0;
}

.offer_section.bottom .sale {
  top: 480px;
  left: 125px;
}

//
.clearfix:after {
  content: '';
  display: block;
  clear: both;
}

