h2.title {
  margin: 0 0 30px;
  font-weight: 700;
  font-size: 32px;
  line-height: 40px;
  text-transform: uppercase;
}

h2.title:after {
  display: block;
  content: '';
  margin: 15px 0 0;
  width: 62px;
  height: 5px;
  background: url(../images/separator_red.png) center no-repeat;
}

h2.title span {
  color: #800000;
  white-space: nowrap;
}

.dark_theme h2.title span {
  color: #f5ca16;
}

.cat {
  padding: 0;
}
.cat > img {
  display: block;
  margin: 0 auto;
  border-radius: 5px;
}
.catitem {
  width: 420px;
  margin: 0 auto 40px;
}
.catitem img {
  display: block;
  margin: 0 auto 10px;
}
.catitem > div {
  width: 420px;
  margin: 0 auto 0px;
  padding: 1px 0 0;
}
.catitem > div h3 {
  display: block;
  font-size: 26px;
  color: #ff8ebd;
  text-shadow: 1px 2px black;
  text-align: center;
  /*color: #03a9f4; */
  /*border-left: 4px solid #03a9f4;*/
  padding: 0 0 0 0px;
  /* line-height: 42px; */
  width: 420px;
  /* float: center; */
  margin: 1px 0 0;
}
.catprice {
  width: 190px;
  float: right;
  text-align: center;
}
.cp-left {
  width: 70px;
  float: left;
  padding-top: 22px;
}
.cp-left span {
  display: inline-block;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.7px;
  background: #ff0000;
  color: #fff;
  border-radius: 4px;
  padding: 0 5px;
}
.cp-left p {
  font-size: 16px;
  font-weight: 700;
  line-height: 30px;
  text-decoration: line-through;
}
.cp-right {
  width: 110px;
  padding-bottom: 50px;
}
.cp-right span {
  font-size: 16px;
  padding-top: 50px;
  color: #000;
  font-weight: 700;
}
.cp-right p {
  font-size: 32px;
  background: #ff0000;
  color: #fff;
  border-radius: 15px;
  width: 150px;
  font-weight: 700;
  line-height: 40px;
  margin: -20px;
}
