}

/* list 1 */

.list1 {
  padding: 0 30px 15px;
  font-size: 0;
  line-height: 0;
  letter-spacing: -1px;
}

.list1 li {
  display: inline-block;
  vertical-align: top;
  margin: 0 20px 15px 0;
  padding: 0 0 0 25px;
  width: 200px;
  font-size: 15px;
  line-height: 22px;
  letter-spacing: normal;
  position: relative;
}

.list1 li:nth-child(even) {
  margin: 0 0 10px;
}

.list1 li:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  content: '';
  border-left: 8px solid rgba(255, 255, 255, 0);
  border-right: 8px solid rgba(255, 255, 255, 0);
  -webkit-transform: rotate(30deg);
  -moz-transform: rotate(30deg);
  -ms-transform: rotate(30deg);
  -o-transform: rotate(30deg);
  transform: rotate(30deg);
  position: absolute;
  top: 8px;
  left: -2px;
}

.list1.marker1 li:before {
  border-top: 8px solid #f5ca16;
}

.list1.marker2 li:before {
  border-top: 8px solid #e43315;
}

/* list 2 */

.list2 {
  padding: 0 30px 15px;
}

.list2 li {
  margin: 0 0 15px;
  padding: 0 0 0 25px;
  position: relative;
}

.list2 li:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  content: '';
  width: 12px;
  height: 12px;
  -webkit-box-shadow: 0 0 0 2px #fff inset;
  -moz-box-shadow: 0 0 0 2px #fff inset;
  box-shadow: 0 0 0 2px #fff inset;
  position: absolute;
  top: 4px;
  left: 0;
}

.list2.marker1 li:before {
  border: 1px solid #e43315;
  background: #e43315;
}

.list2.marker2 li:before {
  border: 1px solid #f5ca16;
  background: #f5ca16;
}

.list2_section {
  padding: 30px 0 0;
}

.dark_theme .list2 li:before {
  -webkit-box-shadow: 0 0 0 2px #222 inset;
  -moz-box-shadow: 0 0 0 2px #222 inset;
  box-shadow: 0 0 0 2px #222 inset;
}

